import React, { Fragment, Component } from 'react'
import 'antd/dist/antd.css';

import Navbar from '../../components/Layout/Navbar/Navbar'

class Layout extends Component {
    render() {
        return (
            <Fragment>
                <Navbar />
                <main>
                    {this.props.children}
                </main>
            </Fragment>
        )
    }
}

export default Layout